.App {
  width:100%;
  height:100%;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#logoBar-mobile{
  display: none;
}
@media screen and (max-width:768px){
  #logoBar-mobile{
      display: block;
  }
  #logoBar-computer{
      display: none;
  }
}